.App {
  text-align: center;
  display: flex;
  flex-wrap: wrap;
}

.react-card-flip {
  width: 150px;
  height: 300px;
  flex: 1 0 20%;
  margin: 5px;
  border: 1px solid rgb(151, 151, 151);
  border-radius: 15px;
  /*background-color: rgb(208, 207, 207);*/
  box-shadow: 1px 1px 5px 0;

}

