  font-size: 6rem;
  font-weight: bold;
}

.react-card-front .card {
  background-color: #ffffff;
  color: #0F184A;
  font-size: 3rem;
}

.card {
  width: 100%;
  height: 100%;
  border-radius: 15px;
}

.card span{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

